// Colors
$blackColor: rgb(22, 22, 22);
$whiteColor: rgb(228, 228, 228);
$redColor: rgb(137, 73, 73);

// Fonts
$mainFont: "Roboto";

// Media
$phone: 640px;
$tablet: 768px;
$desktop: 1024px;
$large: 1200px;

// Mixins
@mixin buttonStyle($color, $fontSize) {
    width: 200px;
    height: 40px;
    background-color: $color;
    border: none;
    border-radius: 8px;
    color: $whiteColor;
    font-size: $fontSize;
}

@mixin centerContent {
    display: grid;
    place-content: center;
}

