.synopsis {
    width: 100%;
    height: 90%;
    border: none;
    resize: none;
    overflow: auto;
    background-color:white;
}

.genres {
    font-size: 13px;
    background-color: rgb(0, 0, 0);
    color: rgb(246, 246, 246);
    padding: 10px;
    border: none;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-align: center;
    box-shadow: 0px 3px rgb(7, 7, 9);
}

.categories-container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: nowrap;
    gap: 5px;
}

.categories {
    margin-bottom: 5px;
    width: 50;
    border: none;
    background-color: rgb(0, 0, 0);
    color: $whiteColor;
    flex-grow: 1;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}